// ChartFilterControls.tsx - Components for filtering chart data
import React, { useState, useMemo } from 'react';
import { parseISO, isValid, format, subDays, subMonths, subWeeks } from 'date-fns';
import { Button } from '@/components/ui/button';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";

// --- Filter Types ---
export type DateFilterType = 'all' | '7days' | '30days' | '90days';
export type MuscleGroupFilter = string | 'all';

// Function to filter logs by date range
export const filterLogsByDateRange = (
  logs: WorkoutLogEntry[], 
  dateFilter: DateFilterType
): WorkoutLogEntry[] => {
  if (dateFilter === 'all') return logs;
  
  const endDate = new Date();
  let startDate: Date;
  
  switch (dateFilter) {
    case '7days':
      startDate = subDays(endDate, 7);
      break;
    case '30days':
      startDate = subDays(endDate, 30);
      break;
    case '90days':
      startDate = subDays(endDate, 90);
      break;
    default:
      return logs;
  }
  
  return logs.filter(log => {
    const parsedDate = parseISO(log.date);
    return isValid(parsedDate) && parsedDate >= startDate && parsedDate <= endDate;
  });
};

// Function to filter logs by muscle group
export const filterLogsByMuscleGroup = (
  logs: WorkoutLogEntry[], 
  muscleGroupFilter: MuscleGroupFilter
): WorkoutLogEntry[] => {
  if (muscleGroupFilter === 'all') return logs;
  
  return logs.filter(log => log.muscleGroup === muscleGroupFilter);
};

// --- Date Range Selector Component ---
interface DateRangeSelectorProps {
  dateFilter: DateFilterType;
  onChange: (filter: DateFilterType) => void;
  className?: string;
}

export const DateRangeSelector: React.FC<DateRangeSelectorProps> = ({ 
  dateFilter, 
  onChange,
  className = ''
}) => {
  return (
    <div className={`flex space-x-1 ${className}`}>
      <Select value={dateFilter} onValueChange={value => onChange(value as DateFilterType)}>
        <SelectTrigger className="h-8 text-xs w-auto min-w-[100px]">
          <SelectValue placeholder="Time Range" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="7days">Last 7 Days</SelectItem>
          <SelectItem value="30days">Last 30 Days</SelectItem>
          <SelectItem value="90days">Last 90 Days</SelectItem>
          <SelectItem value="all">All Time</SelectItem>
        </SelectContent>
      </Select>
    </div>
  );
};

// --- Muscle Group Selector Component ---
interface MuscleGroupSelectorProps {
  logs: WorkoutLogEntry[];
  muscleGroupFilter: MuscleGroupFilter;
  onChange: (filter: MuscleGroupFilter) => void;
  className?: string;
}

export const MuscleGroupSelector: React.FC<MuscleGroupSelectorProps> = ({
  logs,
  muscleGroupFilter,
  onChange,
  className = ''
}) => {
  const muscleGroups = useMemo(() => {
    const groups = [...new Set(logs.map(log => log.muscleGroup))].filter(Boolean);
    return groups.sort();
  }, [logs]);
  
  return (
    <div className={`flex ${className}`}>
      <Select value={muscleGroupFilter} onValueChange={onChange}>
        <SelectTrigger className="h-8 text-xs w-auto min-w-[120px]">
          <SelectValue placeholder="Muscle Group" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="all">All Muscle Groups</SelectItem>
          {muscleGroups.map(group => (
            <SelectItem key={group} value={group}>{group}</SelectItem>
          ))}
        </SelectContent>
      </Select>
    </div>
  );
};

// --- Combined Filter Controls Component ---
interface ChartFilterControlsProps {
  logs: WorkoutLogEntry[];
  dateFilter: DateFilterType;
  onDateFilterChange: (filter: DateFilterType) => void;
  muscleGroupFilter: MuscleGroupFilter;
  onMuscleGroupFilterChange: (filter: MuscleGroupFilter) => void;
  onReset?: () => void;
  showMuscleGroupFilter?: boolean;
  className?: string;
}

export const ChartFilterControls: React.FC<ChartFilterControlsProps> = ({
  logs,
  dateFilter,
  onDateFilterChange,
  muscleGroupFilter,
  onMuscleGroupFilterChange,
  onReset,
  showMuscleGroupFilter = true,
  className = ''
}) => {
  const hasFiltersApplied = dateFilter !== 'all' || muscleGroupFilter !== 'all';
  
  return (
    <div className={`flex flex-wrap items-center gap-2 ${className}`}>
      <DateRangeSelector
        dateFilter={dateFilter}
        onChange={onDateFilterChange}
      />
      
      {showMuscleGroupFilter && (
        <MuscleGroupSelector
          logs={logs}
          muscleGroupFilter={muscleGroupFilter}
          onChange={onMuscleGroupFilterChange}
        />
      )}
      
      {hasFiltersApplied && onReset && (
        <TooltipProvider>
          <Tooltip>
            <TooltipTrigger asChild>
              <Button 
                variant="ghost" 
                size="icon" 
                onClick={onReset}
                className="h-8 w-8"
              >
                <svg 
                  width="14" 
                  height="14" 
                  viewBox="0 0 24 24" 
                  fill="none" 
                  stroke="currentColor" 
                  strokeWidth="2"
                  strokeLinecap="round" 
                  strokeLinejoin="round"
                >
                  <path d="M3 3v18h18" />
                  <path d="M18.37 11.17a6 6 0 0 0-8.5-8.5" />
                  <path d="M21 3h-6v6" />
                </svg>
              </Button>
            </TooltipTrigger>
            <TooltipContent side="bottom">
              <p>Reset Filters</p>
            </TooltipContent>
          </Tooltip>
        </TooltipProvider>
      )}
    </div>
  );
};
