import React, { useEffect } from 'react';
import { DashboardLayout } from '@/components/DashboardLayout';
import { useConsolidatedApp } from '@/hooks/useConsolidatedApp';
import { ViewContent } from '../components/dashboard/ViewContent';
import AuthTest from '../components/AuthTest';

// The main DashboardPage component that wraps content in the layout
const DashboardPage: React.FC = () => {
  const { exercisesStore } = useConsolidatedApp();
  const { fetchExercises } = exercisesStore;

  useEffect(() => {
    fetchExercises();
  }, [fetchExercises]);
  return (
    <DashboardLayout>
      <ViewContent />
    </DashboardLayout>
  );
};

export default DashboardPage;
