// ChartTooltips.tsx - Enhanced custom tooltips for charts
import React from 'react';
import { format } from 'date-fns';
import { TooltipProps } from 'recharts';
import { 
  NameType, 
  ValueType 
} from 'recharts/types/component/DefaultTooltipContent';
import { formatTooltipValue } from './ChartHelpers';

// --- Base Custom Tooltip --- 
const BaseTooltip: React.FC<{
  title?: string;
  children: React.ReactNode;
  className?: string;
}> = ({ title, children, className = '' }) => {
  return (
    <div className={`rounded-md border border-border bg-card px-3 py-2 shadow-md text-card-foreground ${className}`}>
      {title && <div className="text-xs font-medium mb-1">{title}</div>}
      {children}
    </div>
  );
};

// --- Volume Data Tooltip ---
export const VolumeTooltip: React.FC<TooltipProps<ValueType, NameType>> = ({
  active, payload, label
}) => {
  if (!active || !payload || !payload.length) return null;
  
  const data = payload[0].payload;
  const formattedDate = format(new Date(label), 'MMM d, yyyy');
  const volume = Number(data.totalVolume);
  const formattedVolume = volume.toLocaleString(undefined, { maximumFractionDigits: 0 });
  
  return (
    <BaseTooltip title={formattedDate}>
      <div className="flex items-center justify-between">
        <span className="text-xs text-muted-foreground">Total Volume:</span>
        <span className="text-sm font-semibold">{formattedVolume} kg</span>
      </div>
    </BaseTooltip>
  );
};

// --- Detailed Progress Tooltip with Multi-Value Support ---
export const ProgressTooltip: React.FC<TooltipProps<ValueType, NameType>> = ({
  active, payload, label
}) => {
  if (!active || !payload || !payload.length) return null;
  
  const formattedDate = format(new Date(label), 'MMM d, yyyy');
  
  return (
    <BaseTooltip title={formattedDate} className="min-w-[200px]">
      <div className="space-y-1.5">
        {payload.map((entry, index) => {
          const value = Number(entry.value);
          const formattedValue = formatTooltipValue(value, '%');
          const color = entry.color || 'currentColor';
          
          // Add extra description based on entry name
          const description = entry.name === 'Progression' 
            ? 'Progress vs baseline'
            : entry.name === 'Fatigue' 
            ? 'Acute:chronic workload' 
            : '';
            return (
            <div key={index} className="flex flex-col">
              <div className="flex items-center justify-between">
                <div className="flex items-center">
                  <div 
                    className="w-2 h-2 rounded-full mr-1.5" 
                    style={{ backgroundColor: color }}
                  ></div>
                  <span className="text-xs text-muted-foreground">{entry.name}:</span>
                </div>
                <span className="text-sm font-medium">{formattedValue}</span>
              </div>
              {description && (
                <div className="text-[10px] text-muted-foreground ml-3.5 mt-0.5">{description}</div>
              )}
            </div>
          );
        })}
      </div>
    </BaseTooltip>
  );
};

// --- Radar Chart Tooltip ---
export const RadarTooltip: React.FC<TooltipProps<ValueType, NameType>> = ({
  active, payload
}) => {
  if (!active || !payload || !payload.length) return null;
  
  const data = payload[0].payload;
  
  return (
    <BaseTooltip title={data.subject}>
      <div className="space-y-1">
        {payload.map((entry, index) => {
          const value = Number(entry.value);
          return (
            <div key={index} className="flex items-center justify-between">
              <span className="text-xs text-muted-foreground">{entry.name}:</span>
              <span className="text-sm font-medium">{value.toFixed(1)}%</span>
            </div>
          );
        })}
      </div>
    </BaseTooltip>
  );
};

// --- Bar Chart Tooltip ---
export const BarChartTooltip: React.FC<TooltipProps<ValueType, NameType>> = ({
  active, payload
}) => {
  if (!active || !payload || !payload.length) return null;
  
  const data = payload[0].payload;
  
  return (
    <BaseTooltip title={data.muscleGroup}>
      <div className="space-y-1">
        {payload.map((entry, index) => {
          const value = Number(entry.value);
          const formattedValue = entry.name === 'Fatigue' 
            ? `${value.toFixed(1)}%` 
            : value.toLocaleString(undefined, { maximumFractionDigits: 0 }) + ' kg';
            
          return (
            <div key={index} className="flex items-center justify-between">
              <div className="flex items-center">
                <div 
                  className="w-2 h-2 rounded-full mr-1.5" 
                  style={{ backgroundColor: entry.color || 'currentColor' }}
                ></div>
                <span className="text-xs text-muted-foreground">{entry.name}:</span>
              </div>
              <span className="text-sm font-medium">{formattedValue}</span>
            </div>
          );
        })}
      </div>
    </BaseTooltip>
  );
};
