// src/components/dashboard/MuscleFatigueInsights.tsx
import React, { useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { 
  calculateMuscleFatigue, 
  calculateRecoveryProfiles, 
  generateFatigueInsights 
} from '@/utils/muscleFatigueCalculator';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { AlertTriangle, CheckCircle, Info, ArrowRight } from 'lucide-react';
import { useConsolidatedApp } from '@/hooks/useConsolidatedApp';

interface MuscleFatigueInsightsProps {
  logs: WorkoutLogEntry[];
}

export const MuscleFatigueInsights: React.FC<MuscleFatigueInsightsProps> = ({ logs }) => {
  const { appStore } = useConsolidatedApp();
  const { setCurrentView } = appStore;
  
  // Generate insights from workout data
  const insights = useMemo(() => {
    if (!logs || logs.length === 0) return [];
    
    const fatigueData = calculateMuscleFatigue(logs);
    const recoveryProfiles = calculateRecoveryProfiles(logs);
    return generateFatigueInsights(fatigueData, recoveryProfiles);
  }, [logs]);
  
  // Filter insights to get the most relevant ones
  const priorityInsights = useMemo(() => {
    // First include warnings, then positive insights, then info insights
    const warnings = insights.filter(insight => insight.type === 'warning');
    const positive = insights.filter(insight => insight.type === 'positive');
    const info = insights.filter(insight => insight.type === 'info');
    
    return [...warnings, ...positive, ...info].slice(0, 3); // Show top 3 insights
  }, [insights]);
  
  if (!logs || logs.length === 0) {
    return (
      <Card className="h-full">
        <CardHeader className="pb-2">
          <CardTitle className="text-base font-semibold">Muscle Recovery Insights</CardTitle>
          <CardDescription className="text-xs">
            Log workouts to receive personalized recovery insights
          </CardDescription>
        </CardHeader>
        <CardContent className="flex items-center justify-center h-32">
          <p className="text-muted-foreground text-sm">No workout data available</p>
        </CardContent>
      </Card>
    );
  }
  
  if (insights.length === 0) {
    return (
      <Card className="h-full">
        <CardHeader className="pb-2">
          <CardTitle className="text-base font-semibold">Muscle Recovery Insights</CardTitle>
          <CardDescription className="text-xs">
            Log more workouts to generate personalized insights
          </CardDescription>
        </CardHeader>
        <CardContent className="flex items-center justify-center h-32">
          <p className="text-muted-foreground text-sm">Not enough data for insights yet</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="h-full">
      <CardHeader className="pb-2">
        <CardTitle className="text-base font-semibold">Muscle Recovery Insights</CardTitle>
        <CardDescription className="text-xs">
          Personalized insights to optimize recovery and training
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-3">
        {priorityInsights.map((insight, idx) => (
          <div 
            key={idx} 
            className={`p-3 rounded-md 
              ${insight.type === 'warning' ? 'bg-red-500/10' : 
                insight.type === 'positive' ? 'bg-green-500/10' : 
                'bg-blue-500/10'}`}
          >
            <div className="flex items-start gap-2">
              {insight.type === 'warning' ? (
                <AlertTriangle className="h-4 w-4 mt-0.5 text-red-500 shrink-0" />
              ) : insight.type === 'positive' ? (
                <CheckCircle className="h-4 w-4 mt-0.5 text-green-500 shrink-0" />
              ) : (
                <Info className="h-4 w-4 mt-0.5 text-blue-500 shrink-0" />
              )}
              <div>
                <p className="text-sm font-medium mb-1">
                  {insight.muscleGroup}
                </p>
                <p className="text-xs">
                  {insight.insight}
                </p>
                <p className={`text-xs mt-1 font-medium
                  ${insight.type === 'warning' ? 'text-red-500' : 
                    insight.type === 'positive' ? 'text-green-500' : 
                    'text-blue-500'}`}>
                  {insight.suggestion}
                </p>
              </div>
            </div>
          </div>
        ))}
      </CardContent>
      
      {insights.length > 3 && (
        <CardFooter className="pt-0">
          <Button 
            variant="outline" 
            size="sm" 
            onClick={() => setCurrentView('muscleFatigue')}
            className="w-full text-xs text-primary hover:text-primary hover:bg-primary/5 border-primary/20"
          >
            View all insights
            <ArrowRight className="ml-2 h-3 w-3" />
          </Button>
        </CardFooter>
      )}
    </Card>
  );
};
