/* eslint-disable @typescript-eslint/no-explicit-any */
// Modern WorkoutCharts implementation with reusable components
import React, { useMemo, useState } from 'react';
import { 
  ResponsiveContainer, ComposedChart, RadarChart,
  CartesianGrid, XAxis, YAxis, Tooltip, Legend, Area, Radar,
  PolarGrid, PolarAngleAxis, PolarRadiusAxis, ReferenceLine,
  Brush
} from 'recharts';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { format } from 'date-fns';
import { CHART_COLORS } from './ChartConfig';
import { 
  processMuscleGroupVolumeDistributionData,
  processProgressionAndFatigueData
} from './ChartDataProcessing';
import { ProgressionByMuscleChart } from './ProgressionByMuscleChart';
// Inline chart helpers to avoid module issues
import { ZoomControlButton, useChartTheme } from './ChartHelpers';
import {  useCallback } from 'react';

// Inline chart zoom hook
const useChartZoom = (initialDomain: [number, number | 'auto'], minRange = 10) => {
  const [domain, setDomain] = useState<[number, number | 'auto']>(initialDomain);
  const [isZoomed, setIsZoomed] = useState(false);
  
  const handleZoom = useCallback((domain: [number, number | string]) => {
    const [start, end] = domain;
    if (typeof end === 'number' && typeof start === 'number' && end - start < minRange) return;
    setDomain(domain as [number, number | 'auto']);
    setIsZoomed(true);
  }, [minRange]);
  
  const resetZoom = useCallback(() => {
    setDomain(initialDomain);
    setIsZoomed(false);
  }, [initialDomain]);

  return { domain, isZoomed, handleZoom, resetZoom };
};

import { 
  DateFilterType, 
  ChartFilterControls, 
  filterLogsByDateRange 
} from './ChartFilterControls';
import { 
  ProgressTooltip,
  RadarTooltip 
} from './ChartTooltips';

// --- Chart Configuration Constants ---
type AnimationEasing = 'ease' | 'ease-in' | 'ease-out' | 'ease-in-out' | 'linear';

const CHART_ANIMATION = {
  duration: 1500,
  easing: 'ease-out' as AnimationEasing
};

const CHART_MARGINS = {
  standard: { top: 5, right: 10, bottom: 5, left: 0 },
  withLeftAxis: { top: 5, right: 10, bottom: 5, left: 20 }
};

// --- Reusable Chart Card Component ---
interface ChartCardProps {
  title: string;
  description?: string;
  children?: React.ReactNode;
  isEmpty?: boolean;
  emptyMessage?: string;
  className?: string;
  filterControls?: React.ReactNode;
}

const ChartCard = React.forwardRef<HTMLDivElement, ChartCardProps>(
  function ChartCard({
    title,
    description,
    children,
    isEmpty = false,
    emptyMessage = "No data available",
    className = "",
    filterControls
  }, ref) {
    return (
      <Card ref={ref} className={`chart-card ${className}`}>
        <CardHeader className={`pb-2 ${filterControls ? 'flex flex-col sm:flex-row sm:items-center sm:justify-between' : ''}`}>
          <div>
            <CardTitle className="text-base font-semibold">{title}</CardTitle>
            {description && (
              <CardDescription className="text-xs text-muted-foreground">
                {description}
              </CardDescription>
            )}
          </div>
          {filterControls}
        </CardHeader>
        <CardContent className="pt-1">
          <div className="chart-container" style={{ height: 200 }}>
            {isEmpty ? (
              <div className="flex flex-col items-center justify-center h-full p-6 rounded-md border border-dashed border-muted">
                <p className="text-muted-foreground text-sm">{emptyMessage}</p>
              </div>
            ) : (
              children
            )}
          </div>
        </CardContent>
      </Card>
    );
  }
);

// --- Chart Component Props ---
interface ChartComponentProps {
  logs: WorkoutLogEntry[];
}

// Note: VolumeOverTimeChart has been removed

// --- Volume By Muscle Group Bar Chart ---
// Note: VolumeByMuscleGroupChart has been removed

// --- Muscle Group Distribution Radar Chart ---
export const MuscleGroupDistributionChart: React.FC<ChartComponentProps> = ({ logs }) => {
  const [dateFilter, setDateFilter] = useState<DateFilterType>('all');
  const chartTheme = useChartTheme();
  
  // Apply filters to logs
  const filteredLogs = useMemo(() => {
    return filterLogsByDateRange(logs, dateFilter);
  }, [logs, dateFilter]);
  
  const data = useMemo(() => processMuscleGroupVolumeDistributionData(filteredLogs), [filteredLogs]);
  
  // Reset all filters
  const resetFilters = () => {
    setDateFilter('all');
  };
  
  if (!data || data.length === 0) {
    return (
      <ChartCard 
        title="Muscle Group Volume Distribution" 
        isEmpty={true} 
        emptyMessage="Log workouts to see volume distribution."
      />
    );
  }
  
  // Filter controls component
  const filterControls = (
    <ChartFilterControls
      logs={logs}
      dateFilter={dateFilter}
      onDateFilterChange={setDateFilter}
      muscleGroupFilter="all"
      onMuscleGroupFilterChange={() => {}}
      onReset={resetFilters}
      showMuscleGroupFilter={false}
      className="mt-2 sm:mt-0"
    />
  );
  
  return (
    <ChartCard 
      title="Muscle Group Volume Distribution" 
      description="Percentage of total volume per muscle group."
      filterControls={filterControls}
    >
      <ResponsiveContainer width="100%" height="100%">
        <RadarChart 
          data={data} 
          cx="50%" 
          cy="50%" 
          outerRadius="70%"
        >
          <defs>
            <linearGradient id="radarGradient" x1="0" y1="0" x2="1" y2="1">
              <stop offset="0%" stopColor={CHART_COLORS.distribution} stopOpacity={0.9} />
              <stop offset="100%" stopColor={CHART_COLORS.distribution} stopOpacity={0.5} />
            </linearGradient>
          </defs>
          <PolarGrid 
            gridType="circle" 
            stroke={chartTheme.gridColor} 
            strokeWidth={1} 
            opacity={chartTheme.gridOpacity * 2} 
          />
          <PolarAngleAxis 
            dataKey="subject" 
            tickLine={false} 
            axisLine={false} 
            tick={{ 
              fontSize: 12,
              fill: chartTheme.textColor
            }} 
          />
          <PolarRadiusAxis 
            angle={30} 
            domain={[0, 'auto']} 
            tickLine={false} 
            axisLine={false} 
            tick={{ 
              fontSize: 11,
              fill: chartTheme.textColor
            }} 
          />
          <Tooltip 
            content={<RadarTooltip />}
            animationDuration={200}
          />
          <Legend 
            formatter={(value) => <span style={{ fontSize: '12px' }}>{value}</span>}
            iconSize={12}
            wrapperStyle={{ paddingTop: '8px' }}
          />
          <Radar 
            dataKey="value" 
            name="Distribution" 
            fill="url(#radarGradient)" 
            fillOpacity={0.85} 
            stroke={CHART_COLORS.distribution} 
            strokeWidth={2.5}
            strokeOpacity={0.9}
            animationDuration={CHART_ANIMATION.duration}
            animationEasing={CHART_ANIMATION.easing}
            isAnimationActive={true}
          />
        </RadarChart>
      </ResponsiveContainer>
    </ChartCard>
  );
};

// Note: RelativeDailyVolumeChart has been removed

// --- Recent Fatigue By Muscle Group Bar Chart --- 
// Note: FatigueByMuscleGroupChart has been removed

// --- Progression and Fatigue Line Chart ---
export const ProgressionAndFatigueChart: React.FC<ChartComponentProps> = ({ logs }) => {
  const [dateFilter, setDateFilter] = useState<DateFilterType>('all');
  const chartTheme = useChartTheme();
  
  // Apply filters to logs
  const filteredLogs = useMemo(() => {
    return filterLogsByDateRange(logs, dateFilter);
  }, [logs, dateFilter]);
  
  const data = useMemo(() => processProgressionAndFatigueData(filteredLogs), [filteredLogs]);
  
  // Setup zoom functionality
  const { domain, isZoomed, handleZoom, resetZoom } = useChartZoom([0, 'auto']);
  
  // Reset all filters
  const resetFilters = () => {
    setDateFilter('all');
    resetZoom();
  };
  
  if (!data || data.length === 0) {
    return (
      <ChartCard 
        title="Progression & Fatigue Over Time" 
        isEmpty={true} 
        emptyMessage="Log workouts to see progression and fatigue trends."
      />
    );
  }
  
  // Filter controls component
  const filterControls = (
    <ChartFilterControls
      logs={logs}
      dateFilter={dateFilter}
      onDateFilterChange={setDateFilter}
      muscleGroupFilter="all"
      onMuscleGroupFilterChange={() => {}}
      onReset={resetFilters}
      showMuscleGroupFilter={false}
      className="mt-2 sm:mt-0"
    />
  );
    return (    <ChartCard 
      title="Progression & Fatigue Over Time" 
      description="Progression rate and relative workout fatigue levels."
      filterControls={filterControls}
    >
      <div className="relative w-full h-full">
        <ZoomControlButton isZoomed={isZoomed} onReset={resetZoom} />
        
        <ResponsiveContainer width="100%" height="100%">          
          <ComposedChart 
            data={data} 
            margin={CHART_MARGINS.standard}
            onMouseDown={(e) => e && e.activeLabel && handleZoom([e.activeLabel as any, e.activeLabel as any])}
            onMouseMove={(e) => e && e.activeLabel && isZoomed && handleZoom([domain[0] as any, e.activeLabel as any])}
            onMouseUp={() => {}}
          >
            <defs>
              <linearGradient id="colorProgression" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor={CHART_COLORS.progression} stopOpacity={0.4} />
                <stop offset="95%" stopColor={CHART_COLORS.progression} stopOpacity={0.1} />
              </linearGradient>
              <linearGradient id="colorFatigue" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor={CHART_COLORS.fatigue} stopOpacity={0.4} />
                <stop offset="95%" stopColor={CHART_COLORS.fatigue} stopOpacity={0.1} />
              </linearGradient>
            </defs>
            <CartesianGrid 
              vertical={false} 
              strokeDasharray="3 3" 
              strokeOpacity={chartTheme.gridOpacity}
              stroke={chartTheme.gridColor}
            />
            <XAxis 
              dataKey="date" 
              tickFormatter={(str) => format(new Date(str), 'MMM d')} 
              tick={{ fontSize: 12, fill: chartTheme.textColor }}
              tickLine={false} 
              axisLine={false} 
              dy={5}
              minTickGap={15}
              scale="point"
              domain={isZoomed ? domain : ['auto', 'auto']}
            />
            <YAxis 
              yAxisId="left" 
              orientation="left" 
              unit="%" 
              tick={{ fontSize: 12, fill: chartTheme.textColor }}
              tickLine={false} 
              axisLine={false} 
              dx={-5} 
              stroke={CHART_COLORS.progression}
            />
            <YAxis 
              yAxisId="right" 
              orientation="right" 
              unit="%" 
              domain={[0, 'auto']} 
              tick={{ fontSize: 12, fill: chartTheme.textColor }}
              tickLine={false} 
              axisLine={false} 
              dx={-5} 
              stroke={CHART_COLORS.fatigue}
            />
            <Tooltip
              content={<ProgressTooltip />}
              cursor={{ stroke: CHART_COLORS.stroke, strokeWidth: 1, opacity: 0.5 }}
              animationDuration={200}
            />
            <Legend 
              formatter={(value) => <span style={{ fontSize: '12px' }}>{value}</span>}
              iconSize={12}
              wrapperStyle={{ paddingTop: '8px' }}
            />
            <Area 
              yAxisId="left" 
              type="monotone" 
              dataKey="progression" 
              name="Progression" 
              stroke={CHART_COLORS.progression}
              strokeWidth={2}
              fillOpacity={0.7}
              fill="url(#colorProgression)"
              activeDot={{ r: 5, strokeWidth: 1 }}
              animationDuration={CHART_ANIMATION.duration}
              animationEasing={CHART_ANIMATION.easing}
              isAnimationActive={true}
              connectNulls={true}
            />
            <Area 
              yAxisId="right" 
              type="monotone" 
              dataKey="fatigue" 
              name="Fatigue" 
              stroke={CHART_COLORS.fatigue}
              strokeWidth={2} 
              fillOpacity={0.7}
              fill="url(#colorFatigue)"
              activeDot={{ r: 5, strokeWidth: 1 }}
              animationDuration={CHART_ANIMATION.duration}
              animationEasing={CHART_ANIMATION.easing}
              animationBegin={300}
              isAnimationActive={true}
              connectNulls={true}
            />            <ReferenceLine 
              yAxisId="right" 
              y={70} 
              stroke={CHART_COLORS.fatigue} 
              strokeDasharray="3 3" 
              opacity={0.5} 
              label={{ value: "High fatigue", position: "insideBottomRight", fontSize: 10, fill: CHART_COLORS.fatigue }}
            />
            <ReferenceLine 
              yAxisId="left" 
              y={0} 
              stroke={CHART_COLORS.progression} 
              strokeDasharray="3 3" 
              opacity={0.5}
            />
            <Brush 
              dataKey="date"
              height={20}
              stroke={CHART_COLORS.progression}
              fill="transparent"
              tickFormatter={(str) => format(new Date(str), 'MMM d')}
              startIndex={data.length > 30 ? data.length - 30 : 0}
              endIndex={data.length - 1}
            />
          </ComposedChart>
        </ResponsiveContainer>
      </div>
    </ChartCard>
  );
};

// --- Main Export ---
interface WorkoutChartsProps {
  workoutLogs: WorkoutLogEntry[];
}

export const WorkoutCharts: React.FC<WorkoutChartsProps> = ({ workoutLogs }) => {
  return (
    <div className="space-y-8">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <ProgressionAndFatigueChart logs={workoutLogs} />
        <MuscleGroupDistributionChart logs={workoutLogs} />
      </div>
      
      <div className="grid grid-cols-1 gap-6">
        <ProgressionByMuscleChart logs={workoutLogs} />
      </div>
    </div>
  );
};

export default WorkoutCharts;
