// ChartHelpers.tsx - Reusable components and utilities for charts
import React, { useState, useCallback } from 'react';
import { useTheme } from '@/contexts/ThemeContext';

// --- Custom Chart Hooks ---

/**
 * Hook for chart zooming functionality
 * @param initialDomain The initial domain for the chart
 * @param minRange Minimum range allowed for zooming (to prevent excessive zoom)
 */
export const useChartZoom = (initialDomain: [number, number], minRange = 10) => {
  const [domain, setDomain] = useState<[number, number]>(initialDomain);
  const [isZoomed, setIsZoomed] = useState(false);
  
  // Handle zoom event from recharts
  const handleZoom = useCallback((domain: [number, number]) => {
    const [start, end] = domain;
    
    // Prevent zooming beyond minimum range
    if (end - start < minRange) return;
    
    setDomain(domain);
    setIsZoomed(true);
  }, [minRange]);
  
  // Reset zoom to initial domain
  const resetZoom = useCallback(() => {
    setDomain(initialDomain);
    setIsZoomed(false);
  }, [initialDomain]);

  return { domain, isZoomed, handleZoom, resetZoom };
};

/**
 * Custom tooltip formatter for better display
 */
export const formatTooltipValue = (value: number, unit?: string, prefix?: string) => {
  if (value === null || value === undefined) return 'N/A';
  
  let formattedValue = '';
  
  // Format large numbers
  if (value >= 1000000) {
    formattedValue = `${(value / 1000000).toFixed(1)}M`;
  } else if (value >= 1000) {
    formattedValue = `${(value / 1000).toFixed(1)}k`;
  } else if (value % 1 === 0) {
    formattedValue = value.toString();
  } else {
    formattedValue = value.toFixed(1);
  }
  
  return `${prefix || ''}${formattedValue}${unit ? ` ${unit}` : ''}`;
};

/**
 * A reusable zoom control button
 */
export const ZoomControlButton: React.FC<{
  isZoomed: boolean; 
  onReset: () => void;
  className?: string;
}> = ({ isZoomed, onReset, className = '' }) => {
  if (!isZoomed) return null;
  
  return (
    <button
      onClick={onReset}
      className={`absolute right-1 top-1 bg-primary/80 hover:bg-primary text-primary-foreground 
                  text-xs px-2 py-1 rounded-sm shadow-sm flex items-center ${className}`}
      style={{ zIndex: 10 }}
    >
      <svg width="14" height="14" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" className="mr-1">
        <path d="M15 15L21 21M10 17C6.13401 17 3 13.866 3 10C3 6.13401 6.13401 3 10 3C13.866 3 17 6.13401 17 10C17 13.866 13.866 17 10 17Z" 
          stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
      </svg>
      Reset
    </button>
  );
};

/**
 * Helper for ensuring dark/light mode compatibility in charts
 */
export const useChartTheme = () => {
  const { theme } = useTheme();
  
  // Use the centralized theme context instead of manual detection
  const isDarkMode = theme === 'dark' || (theme === 'system' && window.matchMedia('(prefers-color-scheme: dark)').matches);
  
  return {
    isDarkMode,
    gridOpacity: isDarkMode ? 0.1 : 0.15,
    gridColor: isDarkMode ? 'hsl(var(--muted-foreground))' : 'hsl(var(--muted-foreground))',
    textColor: 'hsl(var(--muted-foreground))'
  };
};

/**
 * Enhanced empty chart state with better styling
 */
export const EnhancedEmptyChart: React.FC<{
  message: string;
  icon?: React.ReactNode;
}> = ({ message, icon }) => {
  return (
    <div className="flex flex-col items-center justify-center h-full p-6 rounded-md border border-dashed border-muted">
      {icon || (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="text-muted-foreground mb-2">
          <path d="M3 9H21M7 3V9M17 3V9M7 21H17M7 13H17M7 17H17" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      )}
      <p className="text-muted-foreground text-sm mt-1">{message}</p>
    </div>
  );
};
