import { WorkoutLogEntry } from '@/types/Workout_Log';
import { computeDailyMetrics } from '@/utils/computeDailyMetrics';

// --- Data Processing Functions ---

// Process volume over time data for line/area charts
export const processVolumeOverTimeData = (logs: WorkoutLogEntry[]) => {
  if (!logs || logs.length === 0) return [];
  
  // Group logs by date and calculate daily volume
  const dailyVolumeMap: { [date: string]: number } = {};
  
  logs.forEach(log => {
    const date = log.date;
    const volume = (log.weight || 0) * (log.reps || 0);
    
    if (dailyVolumeMap[date]) {
      dailyVolumeMap[date] += volume;
    } else {
      dailyVolumeMap[date] = volume;
    }
  });
  
  // Convert to array format and sort by date
  return Object.entries(dailyVolumeMap)
    .map(([date, totalVolume]) => ({
      date,
      totalVolume: parseFloat(totalVolume.toFixed(2))
    }))
    .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
};

// Process volume by muscle group data for bar charts
export const processVolumeByMuscleGroupData = (logs: WorkoutLogEntry[]) => {
  if (!logs || logs.length === 0) return [];
  
  const muscleGroupVolume: { [muscleGroup: string]: number } = {};
  
  logs.forEach(log => {
    const group = log.muscleGroup || 'Unknown';
    const volume = (log.weight || 0) * (log.reps || 0);
    muscleGroupVolume[group] = (muscleGroupVolume[group] || 0) + volume;
  });
  
  return Object.entries(muscleGroupVolume)
    .map(([muscleGroup, volume]) => ({
      muscleGroup,
      volume: parseFloat(volume.toFixed(2))
    }))
    .sort((a, b) => b.volume - a.volume);
};

// Process recent fatigue by muscle group data
export const processRecentFatigueByMuscleGroupData = (logs: WorkoutLogEntry[], daysToConsider: number = 7) => {
  if (!logs || logs.length === 0) return [];
  
  // Calculate the cutoff date for recent data
  const cutoffDate = new Date();
  cutoffDate.setDate(cutoffDate.getDate() - daysToConsider);
  
  // Filter logs to only include recent data
  const recentLogs = logs.filter(log => {
    const logDate = new Date(log.date);
    return logDate >= cutoffDate;
  });
  
  if (recentLogs.length === 0) return [];
  
  // Calculate recent volume by muscle group
  const recentMuscleGroupVolume: { [muscleGroup: string]: number } = {};
  recentLogs.forEach(log => {
    const group = log.muscleGroup || 'Unknown';
    const volume = (log.weight || 0) * (log.reps || 0);
    recentMuscleGroupVolume[group] = (recentMuscleGroupVolume[group] || 0) + volume;
  });
  
  // Calculate the maximum single-day total volume for fatigue calculation
  const dailyTotalVolumes: number[] = [];
  const uniqueDates = [...new Set(logs.map(log => log.date))];
  
  uniqueDates.forEach(date => {
    const dayLogs = logs.filter(log => log.date === date);
    const dayVolume = dayLogs.reduce((sum, log) => {
      return sum + ((log.weight || 0) * (log.reps || 0));
    }, 0);
    dailyTotalVolumes.push(dayVolume);
  });
  
  const maxDailyVolume = Math.max(...dailyTotalVolumes, 1); // Use 1 to avoid division by zero
  
  // Calculate fatigue percentage for each muscle group
  return Object.entries(recentMuscleGroupVolume)
    .map(([muscleGroup, volume]) => ({
      muscleGroup,
      fatiguePercent: parseFloat(((volume / maxDailyVolume) * 100).toFixed(2))
    }))
    .sort((a, b) => b.fatiguePercent - a.fatiguePercent);
};

export const processMuscleGroupVolumeDistributionData = (logs: WorkoutLogEntry[]) => {
  if (!logs || logs.length === 0) return [];
  const muscleGroupVolume: { [muscleGroup: string]: number } = {};
  let totalVolumeOverall = 0;

  logs.forEach(log => {
    const group = log.muscleGroup || 'Unknown';
    const volume = (log.weight || 0) * (log.reps || 0);
    muscleGroupVolume[group] = (muscleGroupVolume[group] || 0) + volume;
    totalVolumeOverall += volume;
  });

  if (totalVolumeOverall === 0) {
    return Object.keys(muscleGroupVolume).map(group => ({
      subject: group,
      value: 0,
      fullMark: 100,
    }));
  }

  return Object.entries(muscleGroupVolume)
    .map(([muscleGroup, volume]) => ({
      subject: muscleGroup,
      value: parseFloat(((volume / totalVolumeOverall) * 100).toFixed(2)),
      fullMark: 100,
    }))
    .sort((a, b) => b.value - a.value);
};

export const processProgressionAndFatigueData = (logs: WorkoutLogEntry[]) => {
  if (!logs || logs.length === 0) return [];
  
  // Get unique dates from logs and sort them
  const uniqueDates = [...new Set(logs.map(log => log.date))].sort();
  
  // Calculate daily metrics using the computeDailyMetrics utility
  // First convert WorkoutLogEntry to the expected format for computeDailyMetrics
  const logsForComputation = logs.map(log => [
    log.date, 
    log.muscleGroup, 
    log.exercise, 
    log.reps, 
    log.weight, 
    log.rating
  ]);
  
  const dailyMetrics = computeDailyMetrics(logsForComputation);
  
  // Calculate the maximum volume threshold for fatigue calculation
  const maxThreshold = uniqueDates.reduce((max, date) => {
    const dateLogs = logs.filter(log => log.date === date);
    const dateVolume = dateLogs.reduce((sum, log) => {
      const reps = log.reps || 0;
      const weight = log.weight || 0;
      return sum + reps * weight;
    }, 0);
    return Math.max(max, dateVolume);
  }, 1); // Use 1 to avoid division by zero
  
  // Map each date to the corresponding progression and fatigue values
  return uniqueDates.map(date => {
    // Calculate fatigue based on volume relative to the maximum volume (as percentage)
    const dateLogs = logs.filter(log => log.date === date);
    const dateVolume = dateLogs.reduce((sum, log) => {
      const reps = log.reps || 0;
      const weight = log.weight || 0;
      return sum + reps * weight;
    }, 0);
    const fatigue = parseFloat(((dateVolume / maxThreshold) * 100).toFixed(2)) || 0;
      // Calculate progression rate from daily metrics
    const dayMetrics = dailyMetrics.filter(metric => metric.date === date);
    const progressionRates = dayMetrics.map(metric => 
      metric.progressionRate === "N/A" || !metric.progressionRate
        ? 0 
        : parseFloat(metric.progressionRate) || 0
    );
    const progression = parseFloat(
      (progressionRates.reduce((sum, rate) => sum + rate, 0) / 
      (progressionRates.length || 1)).toFixed(2)
    );
    
    return {
      date,
      fatigue,
      progression,
    };
  });
};
