// src/components/dashboard/MuscleFatigueCard.tsx
import React, { useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Activity, ArrowRight } from 'lucide-react';
import { useConsolidatedApp } from '@/hooks/useConsolidatedApp';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { calculateMuscleFatigue, getMusclesReadyForTraining, getMusclesNeedingRest } from '@/utils/muscleFatigueCalculator';

interface MuscleFatigueCardProps {
  logs: WorkoutLogEntry[];
}

export const MuscleFatigueCard: React.FC<MuscleFatigueCardProps> = ({ logs }) => {
  const { appStore } = useConsolidatedApp();
  const { setCurrentView } = appStore;
  
  // Calculate muscle fatigue data
  const fatigueData = useMemo(() => {
    return calculateMuscleFatigue(logs);
  }, [logs]);
  
  // Get muscles ready for training and those that need rest
  const readyMuscles = useMemo(() => getMusclesReadyForTraining(fatigueData), [fatigueData]);
  const restingMuscles = useMemo(() => getMusclesNeedingRest(fatigueData), [fatigueData]);
  
  if (!logs || logs.length === 0) {
    return (
      <Card className="h-full">
        <CardHeader className="pb-2">
          <CardTitle className="text-base font-semibold flex items-center">
            <Activity className="h-4 w-4 mr-2 text-primary" />
            Muscle Fatigue
          </CardTitle>
          <CardDescription className="text-xs">
            Log workouts to view muscle fatigue data
          </CardDescription>
        </CardHeader>
        <CardContent className="pb-4">
          <div className="flex items-center justify-center h-24">
            <p className="text-muted-foreground text-sm">No workout data</p>
          </div>
        </CardContent>
      </Card>
    );
  }
  
  return (
    <Card className="h-full">
      <CardHeader className="pb-2">
        <CardTitle className="text-base font-semibold flex items-center">
          <Activity className="h-4 w-4 mr-2 text-primary" />
          Muscle Fatigue
        </CardTitle>
        <CardDescription className="text-xs">
          Train fresh muscles, rest fatigued ones
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 gap-4">
          <div>
            <h3 className="text-sm font-medium text-green-500 mb-1">Ready to Train:</h3>
            <div className="flex flex-wrap gap-1">
              {readyMuscles.length > 0 ? (
                readyMuscles.map((muscle, idx) => (
                  <span key={idx} className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-green-500/10 text-green-500">
                    {muscle}
                  </span>
                ))
              ) : (
                <span className="text-xs text-muted-foreground">No muscle groups ready</span>
              )}
            </div>
          </div>
          
          <div>
            <h3 className="text-sm font-medium text-red-500 mb-1">Needs Rest:</h3>
            <div className="flex flex-wrap gap-1">
              {restingMuscles.length > 0 ? (
                restingMuscles.map((muscle, idx) => (
                  <span key={idx} className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-500/10 text-red-500">
                    {muscle}
                  </span>
                ))
              ) : (
                <span className="text-xs text-muted-foreground">No muscle groups need rest</span>
              )}
            </div>
          </div>
        </div>
      </CardContent>
      <CardFooter className="pt-0">
        <Button 
          variant="outline" 
          size="sm" 
          onClick={() => setCurrentView('muscleFatigue')}
          className="w-full text-xs text-primary hover:text-primary hover:bg-primary/5 border-primary/20"
        >
          View detailed fatigue data
          <ArrowRight className="ml-2 h-3 w-3" />
        </Button>
      </CardFooter>
    </Card>
  );
};
