import React from 'react';
import { MuscleFatigueCard } from '../MuscleFatigueCard';
import { MuscleFatigueInsights } from '../MuscleFatigueInsights';
import { useWorkoutLogs } from '@/hooks/useConsolidatedApp';

export const ProgressCard: React.FC = () => {
  const { workoutLogs } = useWorkoutLogs();

  return (
    <div className="grid gap-6">
      <MuscleFatigueCard logs={workoutLogs} />
      <MuscleFatigueInsights logs={workoutLogs} />
    </div>
  );
};
