import React from 'react';
import { DashboardOverview } from './DashboardOverview';
import { RecentWorkouts } from './RecentWorkouts';
import { ActiveWorkoutCard } from './ActiveWorkoutCard';
import { SlideUp, StaggerContainer, StaggerItem } from '@/components/ui/animations';
import { useWorkoutTemplates } from '@/hooks/useConsolidatedApp';
import { QuickActionsGrid, DashboardChartsSection, DashboardSummarySection, ProgressCard } from './cards';

export const DashboardMainContent: React.FC = () => {
  const { activeWorkout } = useWorkoutTemplates();

  return (    
    <StaggerContainer className="flex flex-col gap-6 md:gap-8">
      <StaggerItem>
        <SlideUp>
          <DashboardOverview />
        </SlideUp>
      </StaggerItem>
      
      {/* Active Workout Card - only shows when there is an active workout */}
      {activeWorkout && (
        <StaggerItem>
          <SlideUp delay={0.05}>
            <ActiveWorkoutCard />
          </SlideUp>
        </StaggerItem>      )}
        <StaggerItem>
        <div className="grid gap-6 md:grid-cols-2">
          <SlideUp delay={0.1}>
            <RecentWorkouts />
          </SlideUp>
          
          <SlideUp delay={0.2}>
            <ProgressCard />
          </SlideUp>
        </div>
      </StaggerItem>

      <StaggerItem>
        <QuickActionsGrid />
      </StaggerItem>

      <StaggerItem>
        <DashboardChartsSection />
      </StaggerItem>

      <StaggerItem>
        <DashboardSummarySection />
      </StaggerItem>
    </StaggerContainer>
  );
};
